@import "./Colors.scss";

body {
  background-color: map-get($ProtoColorsHighlights, "White");
  margin: 0;
  padding: 0;
  width: 100vw;
  //height: 100vh;

  .App {
    display: flex;
    flex-direction: column;
    align-items: center;
    .Main {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 80vw;
      max-width: 1000px;
      height: 100vh;
      @include mediaSmall() {
      }
      @include mediaMedium() {
        width: 95vw;
      }
      @include mediaLarge() {
      }
      @include mediaExtra() {
      }
    }
  }
  overflow-x: hidden;
}
* {
  box-sizing: border-box;
}
